import Vue from 'vue';
import App from './App.vue';
import router from './router';
import "./components/filter/dateFormat.js"
// import './plugins/element.js'
//导入字体图标
import './assets/fonts/iconfont.css';
//导入全局样式
import './assets/css/global.css';
//导入第三方组件vue-table-with-tree-grid
import TreeTable from 'vue-table-with-tree-grid';
//导入进度条插件
import NProgress from 'nprogress';
//导入进度条样式
// import 'nprogress/nprogress.css' // #1
// //导入axios
import axios from 'axios';
// //导入vue-quill-editor（富文本编辑器）
import VueQuillEditor from 'vue-quill-editor';
// //导入vue-quill-editor的样式
// import 'quill/dist/quill.core.css' // #2
// import 'quill/dist/quill.snow.css' // #3
// import 'quill/dist/quill.bubble.css' // #4

axios.defaults.baseURL = 'http://127.0.0.1:8888/api/private/v1/';
//请求在到达服务器之前，先会调用use中的这个回调函数来添加请求头信息
axios.interceptors.request.use((config) => {
    //当进入request拦截器，表示发送了请求，我们就开启进度条
    NProgress.start();
    //为请求头对象，添加token验证的Authorization字段
    config.headers.Authorization = window.sessionStorage.getItem('token');
    //必须返回config
    return config;
});
//在response拦截器中，隐藏进度条
axios.interceptors.response.use((config) => {
    //当进入response拦截器，表示请求已经结束，我们就结束进度条
    NProgress.done();
    return config;
});
Vue.prototype.$http = axios;
Vue.config.productionTip = false;
//全局注册组件
Vue.component('tree-table', TreeTable);
//全局注册富文本组件
Vue.use(VueQuillEditor);



new Vue({
    router,
    render: (h) => h(App),
}).$mount('#app');