<template>
<!-- 面包屑导航区域 -->
<div class="user-table">
<el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>用户管理</el-breadcrumb-item>
  <el-breadcrumb-item>用户列表</el-breadcrumb-item>
</el-breadcrumb>
  <!-- 卡片区域 -->
  <el-card>
  <!-- 搜索与添加区域 -->
    <el-row :gutter="20">
    <el-col :span='8'>
          <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
    </el-col>
    <el-col :span='4'>
        <el-button type="primary" @click="addDialogVisible=true" >添加用户</el-button>
    </el-col>
  </el-row>
  <!-- 表格栏 -->
<el-table :data="userlist" border stripe  >
    <el-table-column type="index" label="#"></el-table-column>
    <el-table-column label="姓名" prop="username"></el-table-column>
    <el-table-column label="邮箱" prop="email"></el-table-column>
    <el-table-column label="电话" prop="mobile"></el-table-column>
    <el-table-column label="角色" prop="role_name"></el-table-column>
    <el-table-column label="状态" >
       <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
           </el-switch>                          
       </template>
    </el-table-column>
    <el-table-column label="操作" >
      <template slot-scope="scope">
                <!-- 修改按钮 -->   
            <el-tooltip  effect="light" content="修改" placement="top" :enterable='false' >
                <el-button type="primary" icon="el-icon-edit" circle size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
                <!-- 删除按钮 -->
            <el-tooltip  effect="dark" content="删除" placement="top" :enterable='false'>
              <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="removeUserById(scope.row.id)"></el-button>
            </el-tooltip>
                <!-- 设置按钮 -->
            <el-tooltip  effect="dark" content="分配角色" placement="top" :enterable='false'>
              <el-button type="warning" icon="el-icon-setting" circle size="mini" @click="setRole(scope.row)"></el-button>
            </el-tooltip>
      </template>
    </el-table-column>

</el-table>
<!-- 分页区域 -->
    <el-pagination  @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!-- 添加弹出对话框 -->
<el-dialog
  title="表单提交"
  :visible.sync="addDialogVisible"
  width="50%" @close="adddialogClose">
        <el-form ref="addFormRefs" :model="addForm" label-width="70px" :rules="addFormRules" >
         <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
        </el-form>
 
  <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addUser">确 定</el-button>
  </span>
</el-dialog>
<!-- 修改用户的对话框 -->
<el-dialog
  title="修改用户"
  :visible.sync="editDialogVisible"
  width="35%"
  @close="editDialogClosed">
<el-form ref="editFormRef" :model="editForm" label-width="70px" :rules="editFormRules">
  <el-form-item label="用户名">
    <el-input v-model="editForm.username" disabled></el-input>
  </el-form-item>
    <el-form-item label="邮箱" prop="email">
    <el-input v-model="editForm.email" ></el-input>
  </el-form-item>
    <el-form-item label="手机号" prop="mobile">
    <el-input v-model="editForm.mobile" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editUserInfo">确 定</el-button>
  </span>
</el-dialog>
<!-- 分配角色的对话框 -->
        <el-dialog
          title="分配角色"
          :visible.sync="setRoleDialogVisible"
          width="40%"
          @close="setRoleDialogClose"
          >
                <div>
                  <p>当前用户:{{userInfo.username}}</p>
                  <p>当前角色:{{userInfo.role_name}}</p>
                  <p>分配新角色:
                        <el-select v-model="selecteRoleId" placeholder="请选择">
                           <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
                        </el-select>
                  </p>

                </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="setRoleDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="savaRoleInfo">确 定</el-button>
          </span>
        </el-dialog>
</div>

</template>

<script>
export default {
data() {
      // 验证邮箱的规则
    var checkEmail = (rule, value, cb) => {
      // 验证邮箱的正则表达式
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

      if (regEmail.test(value)) {
        // 合法的邮箱
        return cb()
      }

      cb(new Error('请输入合法的邮箱'))
    }

    // 验证手机号的规则
    var checkMobile = (rule, value, cb) => {
      // 验证手机号的正则表达式
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        return cb()
      }

      cb(new Error('请输入合法的手机号'))
    }
  
  return {
    //验证
    addForm:{
      username:'',
      password:'',
      email:'',
      mobile:'',
    },
    //获取用户列表的参数对象
    queryInfo:{
      query:'',
      pagenum:1,
      //当前每页显示多少条数据
      pagesize:2,
  
    },
    editDialogVisible:false,
    userlist:[],
    total:0,
  addDialogVisible:false,
  // 控制分配角色的显示隐藏
  setRoleDialogVisible:false,
  userInfo:{},
  rolesList:[],
  //已经选中的id值
  selecteRoleId:'',
	//查询到的用户
	editForm:{},
    addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '用户名的长度在3~10个字符之间',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '用户名的长度在6~15个字符之间',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
	  },
	  //修改验证
	   editFormRules: {
        email: [
          { required: true, message: '请输入用户邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入用户手机', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
  }
},
created() {
  this.getUserList()
},
methods: {
  async getUserList(){
    const {data:res} = await this.$http.get('users',{params:this.queryInfo})
    // console.log(res);
    if(res.meta.status!==200) return this.$message.error('获取用户列表失败！')
    this.userlist =res.data.users
    this.total=res.data.total
  },
    //监听pagesize 改变的事件
     handleSizeChange(newSize){
          this.queryInfo.pagesize=newSize
          this.getUserList()
          },
          //监听页码值 改变的事件
     handleCurrentChange(newPage){
          this.queryInfo.pagenum=newPage
          this.getUserList()
          },
          async userStateChanged(val){
           const {data:res} =await this.$http.put(`users/${val.id}/state/${val.mg_state}`)
           if(res.meta.status!==200) {
             val.mg_state=!val.mg_state
             return this.$message.error('更新用户状态失败')
           }
           this.$message.success('更新用户状态成功')
          },

          adddialogClose(){
            this.$refs.addFormRefs.resetFields()
            // this.getUserList()
          }
          ,
     addUser() {
        this.$refs.addFormRefs.validate(async valid => {
          if (!valid) return
          // 可以发起添加用户的网络请求
          const { data: res } = await this.$http.post('users', this.addForm)

          if (res.meta.status !== 201) {
            this.$message.error('添加用户失败！')
          }
          this.$message.success('添加用户成功！')
          // 隐藏添加用户的对话框
          this.addDialogVisible = false
          // 重新获取用户列表数据
          this.getUserList()
        })
    },
    // 修改用户的方法
  async  showEditDialog(id){
		const {data:res} =await this.$http.get('users/'+id)
		if(res.meta.status!==200) return this.$message.error('查询数据失败')
		 this.editForm=res.data
		
		this.editDialogVisible=true
        
    },
//监听修改用户的关闭事件
editDialogClosed(){
	this.$refs.editFormRef.resetFields()
},
//修改用户信息并提交
editUserInfo(){
	//预校验
	this.$refs.editFormRef.validate(async valid=>{
		if(!valid) return 
	const {data:res} = await this.$http.put('users/'+this.editForm.id,{email:this.editForm.email,mobile:this.editForm.mobile})
	if(res.meta.status!==200) return this.$message.error('更新失败！')
	//关闭对话框
	this.editDialogVisible=false
	//刷新
	this.getUserList()
	//提示修改成功 
	this.$message.success('更新用户信息成功')
	})
},
//根据id删除
		async removeUserById(id) {
      // 弹框询问用户是否删除数据
      const confirmResult = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      // 如果用户确认删除，则返回值为字符串 confirm
      // 如果用户取消了删除，则返回值为字符串 cancel
      // console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('删除用户失败！')
      }

      this.$message.success('删除用户成功！')
      this.getUserList()
    },
    // 分配角色
 async setRole(userInfo){
       this.userInfo=userInfo
   const {data:res} = await this.$http.get('roles')
   if(res.meta.status!==200){
     return this.$message.error('获取角色列表失败!')
   }
    this.rolesList=res.data
        this.setRoleDialogVisible=true
    },
    // 修改分配角色
   async savaRoleInfo(){
      if(!this.selecteRoleId){
        return this.$message.error('  错误')
      }
   const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selecteRoleId })
   if(res.meta.status!==200) return this.$message.error('更新角色失败')
   this.$message.success('更新成功')
   this.getUserList()
   this.setRoleDialogVisible =false
    },
    // 监听分配角色的关闭事件
          setRoleDialogClose(){
            this.selecteRoleId=''
            this.userInfo=''
          }
   }
}
</script>

<style lang="less" scoped>
   .el-table {
            /deep/ th{
              text-align: center;
            }
            }
            .el-table{
              /deep/ td{
                text-align: center;
              }
            }

     
</style>